import { call, put, all, takeLatest, cancelled } from "redux-saga/effects";
import qs from "qs";

import {
  INIT_SAVE_SCENARIO,
  INIT_GET_SCENARIO,
  INIT_GET_ALL_SCENARIO,
} from "./constants";
import {
  allUserHandRangesSuccess,
  allUserHandRangesFail,
  getScenarioSuccess,
  getScenarioFail,
  getAllScenarioSuccess,
  getAllScenarioFail,
  saveScenarioSuccess,
} from "./actions";
import request from "../../utils/request";
import { useAuth0 } from "@auth0/auth0-react";

let baseURL = "http://localhost:3000";

/**
 * Get All Hand Ranges request/response handler
 */
export function* saveScenario({
  data: { deadcards, rangeRepoIP, rangeRepoOOP, user, getAccessTokenSilently },
}) {
  const requestUrl = `${baseURL}/api/public`;

  const headers = {
    "Content-Type": "application/json",
    // Bearer: getAccessTokenSilently,
  };

  const body = {
    deadcards,
    rangeRepoIP: rangeRepoIP,
    rangeRepoOOP: rangeRepoOOP,
    user: user,
  };

  const requestParams = {
    body: JSON.stringify(body),
    headers,
    method: "POST",
  };

  try {
    const allRanges = yield call(request, requestUrl, requestParams);
    console.log(allRanges);
    yield put(saveScenarioSuccess(allRanges));
  } catch (err) {
    console.log(err);
  }
}

/**
 * Get All Hand Ranges request/response handler
 */
export function* getScenario({ data }) {
  const requestUrl = `${baseURL}/api/public/get-scenario`;

  try {
    const headers = {
      "Content-Type": "application/json",
      // Bearer: data.getAccessTokenSilently,
    };
    const body = {
      boardcards: data.displayScenario(),
    };

    const requestParams = {
      body: JSON.stringify(body),
      headers,
      method: "POST",
    };

    //yield call/put/cancelled APICALL
    const { rows } = yield call(request, requestUrl, requestParams);
    const response = JSON.parse(rows);
    yield put(getScenarioSuccess(response.t));
    // yield put(allUserHandRangesSuccess(allRanges));
  } catch (err) {
    console.log(err);
    yield put(getScenarioFail(err));

    //yield errorHandling
    // yield put(allUserHandRangesFail(err));
  }
}

/**
 * Get All Hand Ranges request/response handler
 */
export function* getAllScenario({ data }) {
  const requestUrl = `${baseURL}/api/public/get-all-scenario`;

  try {
    const headers = {
      "Content-Type": "application/json",
      // Bearer: data.getAccessTokenSilently,
    };

    const requestParams = {
      headers,
      method: "GET",
    };

    //yield call/put/cancelled APICALL
    const { rows } = yield call(request, requestUrl, requestParams);
    console.log(rows);
    const response = rows.map((row) => JSON.parse(row).t);
    yield put(getAllScenarioSuccess(response));
  } catch (err) {
    console.log(err);
    // yield put(getScenarioFail(err));

    //yield errorHandling
    // yield put(allUserHandRangesFail(err));
  }
}

