/**
 * Root saga manages watcher lifecycle
 */
export default function* handData() {
  yield all([
    takeLatest(INIT_SAVE_SCENARIO, saveScenario),
    takeLatest(INIT_GET_SCENARIO, getScenario),
    takeLatest(INIT_GET_ALL_SCENARIO, getAllScenario),
  ]);
}
